.icon {
  height: 24px;
  width: 24px;
  display: block;
  background-image: url(/src/icons/Icon-Burger-menu.png);
  background-size: cover;

  @include hover(transform, scale(1.2));

  @include onTablet {
    height: 32px;
    width: 32px;
  }

  &--phone {
    background-image: url(/src/icons/Icon-Phone-call.svg);

    &::after {
      content: '+1 234 555-55-55';
      position: absolute;
      display: block;
      transform: scale(0);
      top: 8px;
      right: 30px;
      width: 140px;
      font-size: 12px;
      font-weight: 700;
      letter-spacing: 2px;
      color: rgba(41, 41, 41, 1);
    }

    &:hover::after {
      transform: scale(1);
    }
  }

  &--menu {
    background-image: url(/src/icons/Icon-Burger-menu.svg);

    &:hover {
      background-image: url(/src/icons/Icon-Burger-menu-hover.svg);
    }
  }

  &--сlose {
    background-image: url(/src/icons/Icon-Close.svg);
  }
}
